import React, { useEffect, useState } from "react";
import { Bar } from "react-chartjs-2";
import { addDays, format } from "date-fns";
import CustomDatePicker from "../datepicker";
import { queryReport } from "../AnalyticsReportingApi/queryReport";
import { formatDate, transformToDate } from "../utils";
import {
    ChartTitle,
    ReportWrapper,
    Subtitle,
    DatepickerRow,
    ChartWrapper,
    colors,
} from "../styles";
import { queryReport2 } from "../AnalyticsReportingApi/queryReport2";
import { queryReport3 } from "../AnalyticsReportingApi/queryReport3";


const Visual_usertype_report = (props) => {
    const INITIAL_STATE = {
        labels: [],
        datasets: [],
    };
    const [reportData, setReportData] = useState(INITIAL_STATE);
    const [startDate, setStartDate] = useState(addDays(new Date(), -10));
    const [endDate, setEndDate] = useState(new Date());
    const [totalSources, setTotalSources] = useState(0);

    const transformAPIData = (data) => {
        let transformedData = [];
        let datesArray = [];
        data.forEach((row) => {
            transformedData.push({
                date: formatDate(row.dimensions[1]),
                source: row.dimensions[0],
                visits: row.metrics[0].values[0],
            });
            datesArray.push(transformToDate(row.dimensions[1]));
        });
        return [transformedData, datesArray];
    };

    const groupDataBySource = (data) => {
        return data.reduce((r, a) => {
            r[a.source] = r[a.source] || [];
            r[a.source].push(a);
            return r;
        }, Object.create(null));
    };

    const sortSourcesByTotalVisits = (data) => {
        let sumedVisits = [];
        for (let [key, value] of Object.entries(data)) {
            const sumOfVisits = value.reduce((a, b) => {
                return a + parseInt(b.visits);
            }, 0);
            sumedVisits.push({
                source: key,
                visits: sumOfVisits,
            });
        }
        return sumedVisits.sort((a, b) => b.visits - a.visits);
    };

    const createDataForChart = (datesArray, sumedVisits, groupedBySource) => {
        datesArray.sort((a, b) => {
            return new Date(a) - new Date(b);
        });
        const datesFormatted = datesArray.map((date) =>
            format(new Date(date), "MMM. d, yyyy")
        );
        const uniqueDates = [...new Set(datesFormatted)];
        let datasetsArray = [];
        let i = 0;
        sumedVisits.forEach((item, id) => {
            if (id < 5) {
                const label = item.source;
                const backgroundColor = colors[i + 3];
                i++;
                let data = [];
                uniqueDates.forEach((date) => {
                    const row = groupedBySource[item.source].find(
                        (item) => item.date === date
                    );
                    if (row) {
                        data.push(parseInt(row.visits));
                    } else {
                        data.push(0);
                    }
                });
                datasetsArray.push({
                    label,
                    backgroundColor,
                    data,
                });
            }
        });
        return { labels: uniqueDates, data: datasetsArray };
    };

    const displayResults = (response) => {
        const queryResult = response.result.reports[0].data.rows;

        const data = transformAPIData(queryResult);
        let transformedData = data[0];
        let datesArray = data[1];

        const groupedBySource = groupDataBySource(transformedData);
        setTotalSources(Object.keys(groupedBySource).length);

        const sumedVisits = sortSourcesByTotalVisits(groupedBySource);

        const dataForChart = createDataForChart(
            datesArray,
            sumedVisits,
            groupedBySource
        );

        setReportData({
            ...reportData,
            labels: dataForChart.labels,
            datasets: dataForChart.data,
        });
    };

    const options = {
        tooltips: {
            displayColors: true,
            callbacks: {
                mode: "x",
            },
        },
        scales: {
            xAxes: [
                {
                    stacked: true,
                    gridLines: {
                        display: false,
                    },
                },
            ],
            yAxes: [
                {
                    stacked: true,
                    ticks: {
                        beginAtZero: true,
                    },
                    type: "linear",
                },
            ],
        },
        maintainAspectRatio: false,
        legend: { position: "bottom" },
        plugins: {
            datalabels: {
                font: {
                    size: 0,
                },
            },
        },
    };

    const data = {
        labels: reportData.labels,
        datasets: reportData.datasets,
    };

    useEffect(() => {
        const request = {
            viewID: "241653669",
            startDate,
            endDate,
            metrics: props.metric,
            dimensions: props.dimension,
        };
        setTimeout(
            () =>
                queryReport3(request)
                    .then((resp) => displayResults(resp))
                    .catch((error) => console.error(error)),
            1500
        );
    }, [startDate, endDate]);

    return (
        <ReportWrapper>
            <ChartTitle>{`${props.title}`}</ChartTitle>
            <Subtitle>{`${props.title} - ${totalSources}`}</Subtitle>
            <DatepickerRow>
                <CustomDatePicker
                    placeholder={"Start date"}
                    date={startDate}
                    handleDateChange={(date) => setStartDate(date)}
                />
                <CustomDatePicker
                    placeholder={"End date"}
                    date={endDate}
                    handleDateChange={(date) => setEndDate(date)}
                />
            </DatepickerRow>
            {reportData && (
                <ChartWrapper>
                    <Bar data={data} width={100} height={250} options={options} />
                </ChartWrapper>
            )}
        </ReportWrapper>
    );
};

export default Visual_usertype_report;
